.App {
  text-align: center;
  background-color: #1e1e1e;
  color: #ffffff;
}

.App-header {
  background-color: #282c34;
  padding: 20px;
}

.App-header h1 {
  color: #0098ea;
}

nav ul {
  list-style-type: none;
  padding: 0;
}

nav ul li {
  display: inline;
  margin: 0 15px;
  color: #ffffff;
}

main {
  padding: 20px;
}

button {
  background-color: #0098ea;
  color: #ffffff;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
}

button:hover {
  background-color: #007bb5;
}

.domain {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  border: 1px solid #007bb5;
  margin: 20px 0;
}
